@font-face {
  font-family: "Black";
  src: url("../public/font/Pretendard-Black.ttf");
}

@font-face {
  font-family: "Bold";
  src: url("../public/font/Pretendard-Bold.ttf");
}

@font-face {
  font-family: "Medium";
  src: url("../public/font/Pretendard-Medium.ttf");
}

:root {
  --font-900: 'Black';
  --font-700: 'Bold';
  --font-500: 'Medium'
}

body,
html {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  overflow-x: hidden;
  font-family: var(--font-500);
  background: linear-gradient(rgb(34, 40, 49), rgb(14, 14, 14));
}


nav {
  width: 80%;
  color: lightgray;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1000;

  ul {
    display: flex;
    justify-content: end;
    gap: 2rem;
    list-style: none;

    li {
      cursor: pointer;

      &:hover {
        color: lightyellow;
      }

      span {
        margin-left: 0.3rem;
      }
    }

    .selected {
      color: bisque;
    }
  }
}

/* main------------------------------------------------ */

.main-container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  margin: 0 auto;
  max-width: 1200px;
  height: 100vh;

  .main-left {
    width: 40%;
  }

  .main-right {
    width: 60%;
  }

  .main-img {
    max-width: 15rem;
    margin: 0 auto;

    img {
      width: 100%;
      height: 100%;
      border-radius: 10rem;
      box-shadow: 0px 0px 25px 0px rgba(143, 143, 143, 0.75);
    }
  }

  .main-text {
    font-size: 1.3rem;
    color: whitesmoke;
    text-align: center;
    width: 100%;

    span {
      color: lemonchiffon;
    }

    p {
      margin-bottom: 4rem;
    }
  }

  .main-btn {
    border: none;
    display: block;
    text-align: center;
    cursor: pointer;
    overflow: hidden;
    position: relative;
    font-weight: 700;
    font-size: 1rem;
    background-color: lavenderblush;
    color: black;
    padding: 0.5rem;
    margin: 2rem auto;
    box-shadow: 0 5px 15px rgba(126, 125, 125, 0.6);
    border-radius: 1rem;

    span {
      position: relative;
      z-index: 1;
    }

    &::after {
      content: "";
      position: absolute;
      left: 0;
      top: 0;
      width: 200%;
      height: 500%;
      background-color: lavender;
      transition: all .4s ease-in-out;
      transform: translateX(-98%) translateY(-25%) rotate(45deg);
    }

    &:hover::after {
      transform: translateX(-9%) translateY(-25%) rotate(45deg);
      transform: translateX(-9%) translateY(-25%) rotate(45deg);
    }
  }
}


/* skills------------------------------------------ */
.skills-project {
  background: linear-gradient(rgb(38, 38, 61), rgb(14, 14, 14));
}

.skills-container {
  width: 100%;
  margin: 0 auto;
  max-width: 1200px;
  min-height: 100vh;
  justify-content: center;
  align-items: center;
  color: whitesmoke;
  display: grid;
  grid-template-columns: 1fr 1fr;
  padding: 2rem;

  .skills-box {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 0 auto;
    border: 0.2rem solid rgb(0, 173, 181);
    border-radius: 1rem;
    list-style: none;

    &:last-child {
      grid-column: 1 / 3;
    }
  }

  .skills-title {
    font-family: var(--font-700);
    position: absolute;
    top: -1.3rem;
    background-color: rgb(34, 40, 49);
    border: 0.2rem solid rgb(0, 173, 181);
    border-radius: 0.4rem;
    padding: 0.3rem;
  }

  .skills-btn {
    background: rgb(34, 40, 49);
    color: beige;
    letter-spacing: 0.1rem;
    position: absolute;
    top: -1.5rem;
    left: -1rem;
    border: 0.2rem solid rgb(124, 233, 238);
    border-radius: 0.3rem;
    padding: 0.5rem;
    width: 4rem;
    transform: rotate(-20deg);
    cursor: pointer;
    transition: all 1s;

    &:hover {
      background: rgb(0, 173, 181);
      color: white;
    }
  }

  .skills-content {
    margin: 0.8rem;

    li {
      margin: 1rem;
    }
  }
}


/* project------------------------------------------------ */
.pink {
  border: 0.2rem solid thistle;
  margin-right: auto;

  .project-title {
    color: white;
    background-color: rgb(34, 40, 49);
    border: 0.2rem solid thistle;
  }

  .project-icon {
    color: thistle
  }

  .project-period {
    border: 1px dashed thistle;
    border-radius: 2rem;
  }
}

.blue {
  border: 0.2rem solid lightblue;
  margin: 3rem 0 3rem auto;

  .project-title {
    background-color: rgb(34, 40, 49);
    border: 0.2rem solid lightblue;
    color: white;
  }

  .project-icon {
    color: lightblue
  }

  .project-period {
    border: 1px dashed lightblue;
    border-radius: 2rem;
  }
}


.project {
  font-family: var(--font-500);
  color: white;

  .project-header {
    color: white;
    font-size: 4rem;
    text-align: center;
    text-shadow: 4px 4px 2px rgba(255, 255, 255, 0.6);

    span {
      color: #78c7d2;
    }
  }

  .project-container {
    width: 80%;
    margin: 0 auto;
    max-width: 1200px;

    .project-box {
      display: flex;
      justify-content: center;
      max-width: 40rem;
      border-radius: 0.4rem;

      .project-left {
        width: 50%;

        .project-title {
          position: absolute;
          top: -1.2rem;
          left: -1rem;
          padding: 0.3rem;
          border-radius: 0.2rem;
          font-family: var(--font-700);
        }

        .project-period {
          color: #ccc;
          position: absolute;
          top: -1.2rem;
          right: 0.5rem;
          background-color: rgb(34, 40, 49);
          padding: 0.3rem;

          span {
            color: coral;
          }
        }

        .project-content {
          margin-top: 1rem;
          padding: 0.5rem;
          list-style: none;

          li {
            padding: 0.5rem;

            &:first-child {
              color: bisque;

              span {
                opacity: 0.5;
                color: gainsboro;
              }
            }
          }
        }
      }

      .project-right {
        width: 50%;

        .project-img {
          display: flex;
          height: 90%;

          .project-img-box {
            margin: 0.5rem;

            img {
              width: 100%;
              height: 100%;
              border-radius: 0.2rem;
            }
          }
        }

        .project-icon {
          display: flex;
          justify-content: space-around;

          .icon-box {
            min-width: 5rem;
          }

          .icon-git,
          .icon-link {
            margin-left: 1rem;

            &:hover,
            &:hover {
              cursor: pointer;
              color: white;
            }
          }
        }
      }
    }
  }
}


@media screen and (max-width: 768px) {
  .main-container {
    display: flex;
    flex-direction: column-reverse;

    .main-left {
      width: 90%;
      margin: 0 auto;

      .main-img {
        margin: 0 auto;
        width: 9rem;
      }
    }

    .main-right {
      width: 90%;
      margin: 3rem auto;

      .main-text {
        font-size: 0.75rem;
      }
    }
  }

  .skills-container {
    display: block;
    width: 80%;
    margin: 5rem auto;
    padding: 0;

    .skills-box {
      margin-top: 2rem;
      white-space: nowrap;
    }

    .skills-title {
      font-size: 0.8rem;
    }

    .skills-btn {
      width: 3.5rem
    }

    .skills-content {
      font-size: 0.9rem;
    }
  }

  .project-left {
    width: 50%;
  }

  .project-content {
    li {
      font-size: 0.7rem;
    }
  }
}